import {
  h,
  Component,
  FunctionalComponent,
  Host,
  State,
  Method,
  Element,
  Fragment,
  Listen,
} from "@stencil/core";
import { SystemNotificationInterface } from "../../../interfaces/components/messages/system-notifications/system-notifications";
import CloseIcon from "@material-design-icons/svg/round/close.svg";

/**
 * This is a component for system notifications.
 */
@Component({
  tag: "rwc-system-notifications",
  styleUrl: "system-notifications.scss",
  shadow: true,
})
export class RwcSystemNotification {
  /**
   * The reference to the host element.
   */
  @Element() host!: HTMLRwcSystemNotificationsElement;

  /**
   * The reference to the notification container element.
   */
  private notificationContainerElement!: HTMLDivElement;

  /**
   * This state holds the notifications.
   */
  @State() notifications: Map<
    SystemNotificationInterface,
    NodeJS.Timeout | null
  > = new Map();

  /**
   * This state is used to trigger changes.
   */
  @State() stateChangeTrigger: boolean = false;

  /**
   * This method gets the animation duration from the host element style.
   */
  private get animationDuration(): number {
    return Number.parseInt(
      getComputedStyle(this.host).getPropertyValue(
        "--system-notifications-fade-animation-duration"
      )
    );
  }

  /**
   * This method gets the close animation duration from the host element style.
   */
  private get heightAnimationDuration(): number {
    return Number.parseInt(
      getComputedStyle(this.host).getPropertyValue(
        "--system-notifications-height-animation-duration"
      )
    );
  }

  /**
   * Sets the max height of each of notifications wrapper to size of its content.
   */
  @Listen("resize", { target: "window" })
  onWindowResize(): void {
    this.notificationContainerElement
      .querySelectorAll(".notification-wrapper")
      .forEach((wrapper: HTMLDivElement) => {
        wrapper.style.maxHeight = `${wrapper.scrollHeight}px`;
      });
  }

  /**
   * Initialzies the timeout callbacks for any notifications existing in component.
   */
  public connectedCallback(): void {
    this.notifications.clear();
  }

  /**
   * Clears the timout callbacks.
   */
  public disconnectedCallback() {
    this.notifications.forEach((timeout) => {
      clearTimeout(timeout);
    });
  }

  /**
   * This method pushes a notification. Generates random key if not set.
   * @returns {string} The notification key.
   */
  @Method()
  public async pushNotification(
    notification: SystemNotificationInterface
  ): Promise<string> {
    if (!notification.key) {
      notification.key = Math.floor(
        Math.random() * Number.MAX_SAFE_INTEGER
      ).toString();
    }

    this.notifications.set(notification, this.initAutoRemove(notification));

    if (this.forceStateChange()) {
      await new Promise(requestAnimationFrame);

      // Calculate and set the height after the browser has had a chance to render the element
      setTimeout(() => {
        this.updateNotificationMaxHeight(notification);
      }, 0);
    }
    return notification.key;
  }

  /**
   * This method sets notifications.
   */
  @Method()
  public async setNotifications(
    notifications: SystemNotificationInterface[]
  ): Promise<string[]> {
    this.clearNotifications();
    const keys: string[] = [];
    for (const notification of notifications) {
      keys.push(await this.pushNotification(notification));
    }

    return keys;
  }

  /**
   * This method removes a notification.
   */
  @Method()
  public async removeNotification(index: number): Promise<void> {
    const key = Array.from(this.notifications.keys())[index];
    this.removeNotificationByReference(key);
  }

  /**
   * This method clears all notifications.
   */
  @Method()
  public async clearNotifications(): Promise<void> {
    this.notifications.clear();
    this.forceStateChange();
  }

  /**
   * This method initializes auto removal of a notification.
   */
  private initAutoRemove(
    notification: SystemNotificationInterface
  ): NodeJS.Timeout | null {
    this.unsetAutoRemoveByReference(notification);
    if (notification.duration > 0) {
      return setTimeout(() => {
        this.removeNotificationByReference(notification);
      }, notification.duration);
    }

    return null;
  }

  /**
   * This method unsets auto removal of a notification by reference.
   */
  private unsetAutoRemoveByReference(
    notification: SystemNotificationInterface
  ): void {
    const timeout = this.notifications.get(notification);
    if (timeout !== null) {
      clearTimeout(timeout);
    }
  }

  /**
   * This method removes a notification by reference.
   */
  private async removeNotificationByReference(
    notification: SystemNotificationInterface
  ): Promise<void> {
    setTimeout(() => {
      notification.closing = true;

      if (this.forceStateChange()) {
        setTimeout(() => {
          this.handleNotificationRemove(notification);
        }, Math.max(0, this.animationDuration - this.heightAnimationDuration));
      }
    }, 0);
  }

  /**
   * Awaits end of closing animation and removes the notification from the list.
   * @param notification The notification to remove.
   */
  private async handleNotificationRemove(
    notification: SystemNotificationInterface
  ): Promise<void> {
    setTimeout(() => {
      this.notifications.delete(notification);
      this.forceStateChange();
    }, Math.max(0, this.heightAnimationDuration));
  }

  /**
   * Updates the max-height style attribute to the current height needed to render content.
   * @param {SystemNotificationInterface} notification The notification to update the height for.
   * @returns {HTMLElement} The html element of the notification.
   */
  private updateNotificationMaxHeight(
    notification: SystemNotificationInterface
  ): HTMLElement {
    const notificationElement: HTMLElement = this.notificationContainerElement.querySelector(
      `[data-key="${notification.key}"]`
    );

    if (notificationElement) {
      notificationElement.style.maxHeight = `${notificationElement.scrollHeight}px`;
    }

    return notificationElement;
  }

  /**
   * This method forces a state change.
   */
  private forceStateChange(): boolean {
    try {
      this.stateChangeTrigger = !this.stateChangeTrigger;
      return true;
    } catch (e) {
      return false;
    }
  }

  /**
   * This method renders the component.
   */
  public render(): FunctionalComponent {
    return (
      <Host>
        <div
          ref={(element: HTMLDivElement) =>
            (this.notificationContainerElement = element)
          }
          class="container"
        >
          {this.renderNotifications()}
        </div>
      </Host>
    );
  }

  /**
   * This method renders notifications.
   */
  private renderNotifications(): FunctionalComponent[] {
    const notificationElements: FunctionalComponent[] = [];
    for (const [notification, _timeout] of this.notifications) {
      notificationElements.push(
        <div
          class={{
            ["notification-wrapper"]: true,
            closing: notification.closing,
          }}
          key={notification.key}
          data-key={notification.key}
          onMouseEnter={() => this.unsetAutoRemoveByReference(notification)}
          onMouseLeave={() => this.initAutoRemove(notification)}
        >
          <div
            class={{
              notification: true,
              [notification.type ?? "info"]: true,
            }}
          >
            {this.renderCloseIcon(notification)}
            {notification.title && <p class="title">{notification.title}</p>}
            {notification.message && (
              <p class="message">{notification.message}</p>
            )}
          </div>
        </div>
      );
    }
    return notificationElements;
  }

  /**
   * This method renders the close icon for a notification.
   */
  private renderCloseIcon(
    notification: SystemNotificationInterface
  ): FunctionalComponent {
    if (notification.closable) {
      return (
        <div
          class="close-icon"
          innerHTML={CloseIcon}
          onClick={() => this.removeNotificationByReference(notification)}
        ></div>
      );
    }

    return <Fragment></Fragment>;
  }
}
