@import "global.scss";

:host {
  /**
   * @prop --system-notifications-info-title-color: Title color for info notifications.
   * @prop --system-notifications-info-message-color: Message color for info notifications.
   * @prop --system-notifications-info-background-color: Background color for info notifications.
   * @prop --system-notifications-info-border-color: Border color for info notifications.
   * @prop --system-notifications-info-close-icon-color: Close icon color for info notifications.
   * @prop --system-notifications-info-close-icon-hover-color: Close icon hover color for info notifications.
   * 
   * @prop --system-notifications-success-title-color: Title color for success notifications.
   * @prop --system-notifications-success-message-color: Message color for success notifications.
   * @prop --system-notifications-success-background-color: Background color for success notifications.
   * @prop --system-notifications-success-border-color: Border color for success notifications.
   * @prop --system-notifications-success-close-icon-color: Close icon color for success notifications.
   * @prop --system-notifications-success-close-icon-hover-color: Close icon hover color for success notifications.
   * 
   * @prop --system-notifications-warning-title-color: Title color for warning notifications.
   * @prop --system-notifications-warning-message-color: Message color for warning notifications.
   * @prop --system-notifications-warning-background-color: Background color for warning notifications.
   * @prop --system-notifications-warning-border-color: Border color for warning notifications.
   * @prop --system-notifications-warning-close-icon-color: Close icon color for warning notifications.
   * @prop --system-notifications-warning-close-icon-hover-color: Close icon hover color for warning notifications.
   * 
   * @prop --system-notifications-error-title-color: Title color for error notifications.
   * @prop --system-notifications-error-message-color: Message color for error notifications.
   * @prop --system-notifications-error-background-color: Background color for error notifications.
   * @prop --system-notifications-error-border-color: Border color for error notifications.
   * @prop --system-notifications-error-close-icon-color: Close icon color for error notifications.
   * @prop --system-notifications-error-close-icon-hover-color: Close icon hover color for error notifications.
   * 
   * @prop --system-notifications-border-width: Border width for notifications.
   * @prop --system-notifications-border-style: Border style for notifications.
   * @prop --system-notifications-border-radius: Border radius for notifications.
   * 
   * @prop --system-notifications-item-padding: Padding for notification items.
   * @prop --system-notifications-item-gap: Gap between notification items.
   * 
   * @prop --system-notifications-close-icon-size: Size of the close icon.
   * @prop --system-notifications-close-icon-spacing-right: Spacing to the right of the close icon.
   * 
   * @prop --system-notifications-title-font-size: Font size for the title.
   * @prop --system-notifications-title-font-weight: Font weight for the title.
   * 
   * @prop --system-notifications-message-font-size: Font size for the message.
   * @prop --system-notifications-message-font-weight: Font weight for the message.
   * 
   * @prop --system-notifications-fade-animation-duration: Duration of the fade animation.
   * @prop --system-notifications-height-animation-duration: Duration of the open and close animation. Open animation is 2x faster.
   */
  --system-notifications-info-title-color: #{$rwc-color-text-primary};
  --system-notifications-info-message-color: #{$rwc-color-grey-900};
  --system-notifications-info-background-color: #{$rwc-color-blue-300};
  --system-notifications-info-border-color: #{$rwc-color-blue-600};
  --system-notifications-info-close-icon-color: #{$rwc-color-text-primary};
  --system-notifications-info-close-icon-hover-color: #{$rwc-color-grey-900};

  --system-notifications-success-title-color: #{$rwc-color-text-primary};
  --system-notifications-success-message-color: #{$rwc-color-grey-900};
  --system-notifications-success-background-color: #{$rwc-color-green-500};
  --system-notifications-success-border-color: #{$rwc-color-green-700};
  --system-notifications-success-close-icon-color: #{$rwc-color-text-primary};
  --system-notifications-success-close-icon-hover-color: #{$rwc-color-grey-900};

  --system-notifications-warning-title-color: #{$rwc-color-text-primary};
  --system-notifications-warning-message-color: #{$rwc-color-grey-900};
  --system-notifications-warning-background-color: #{$rwc-color-orange-300};
  --system-notifications-warning-border-color: #{$rwc-color-orange-600};
  --system-notifications-warning-close-icon-color: #{$rwc-color-text-primary};
  --system-notifications-warning-close-icon-hover-color: #{$rwc-color-grey-900};

  --system-notifications-error-title-color: #{$rwc-color-text-primary};
  --system-notifications-error-message-color: #{$rwc-color-grey-900};
  --system-notifications-error-background-color: #{$rwc-color-red-300};
  --system-notifications-error-border-color: #{$rwc-color-red-600};
  --system-notifications-error-close-icon-color: #{$rwc-color-text-primary};
  --system-notifications-error-close-icon-hover-color: #{$rwc-color-grey-900};

  --system-notifications-border-width: 2px;
  --system-notifications-border-style: solid;
  --system-notifications-border-radius: 0.5rem;

  --system-notifications-item-padding: #{$rwc-spacing--xs} #{$rwc-spacing--xxl}
    #{$rwc-spacing--xs} #{$rwc-spacing--xs};
  --system-notifications-item-gap: #{$rwc-spacing--3xs};

  --system-notifications-close-icon-size: 1.5rem;
  --system-notifications-close-icon-spacing-right: #{$rwc-spacing--xs};

  --system-notifications-title-font-size: #{$rwc-font-size-body-sm};
  --system-notifications-title-font-weight: 600;

  --system-notifications-message-font-size: #{$rwc-font-size-label-sm};
  --system-notifications-message-font-weight: 500;

  --system-notifications-fade-animation-duration: 500ms;
  --system-notifications-height-animation-duration: 200ms;

  display: block;
}

p {
  margin: 0;
}

.container {
  display: flex;
  flex-direction: column;
  gap: var(--system-notifications-item-gap);
  position: relative;
}

.notification-wrapper {
  box-sizing: border-box;
  width: 100%;
  overflow: hidden;
  max-height: 0;

  transition: opacity var(--system-notifications-fade-animation-duration),
    max-height calc(var(--system-notifications-height-animation-duration) / 2) linear;

  &.closing {
    max-height: 0 !important;
    opacity: 0;

    transition: opacity var(--system-notifications-fade-animation-duration),
      max-height var(--system-notifications-height-animation-duration) linear
        calc(
          var(--system-notifications-fade-animation-duration) -
            var(--system-notifications-height-animation-duration)
        );
  }
}

.notification {
  --system-notifications-background-color: var(
    --system-notifications-info-background-color
  );
  --system-notifications-border-color: var(
    --system-notifications-info-border-color
  );
  --system-notifications-title-color: var(
    --system-notifications-info-title-color
  );
  --system-notifications-message-color: var(
    --system-notifications-info-message-color
  );
  --system-notifications-close-icon-color: var(
    --system-notifications-info-close-icon-color
  );
  --system-notifications-close-icon-hover-color: var(
    --system-notifications-info-close-icon-hover-color
  );

  background-color: var(--system-notifications-background-color);
  border: var(--system-notifications-border-width)
    var(--system-notifications-border-style)
    var(--system-notifications-border-color);

  border-radius: var(--system-notifications-border-radius);
  opacity: 1;
  padding: var(--system-notifications-item-padding);
  position: relative;

  &.success {
    --system-notifications-background-color: var(
      --system-notifications-success-background-color
    );
    --system-notifications-border-color: var(
      --system-notifications-success-border-color
    );
    --system-notifications-title-color: var(
      --system-notifications-success-title-color
    );
    --system-notifications-message-color: var(
      --system-notifications-success-message-color
    );
    --system-notifications-close-icon-color: var(
      --system-notifications-success-close-icon-color
    );
    --system-notifications-close-icon-hover-color: var(
      --system-notifications-success-close-icon-hover-color
    );
  }

  &.warning {
    --system-notifications-background-color: var(
      --system-notifications-warning-background-color
    );
    --system-notifications-border-color: var(
      --system-notifications-warning-border-color
    );
    --system-notifications-title-color: var(
      --system-notifications-warning-title-color
    );
    --system-notifications-message-color: var(
      --system-notifications-warning-message-color
    );
    --system-notifications-close-icon-color: var(
      --system-notifications-warning-close-icon-color
    );
    --system-notifications-close-icon-hover-color: var(
      --system-notifications-warning-close-icon-hover-color
    );
  }

  &.error {
    --system-notifications-background-color: var(
      --system-notifications-error-background-color
    );
    --system-notifications-border-color: var(
      --system-notifications-error-border-color
    );
    --system-notifications-title-color: var(
      --system-notifications-error-title-color
    );
    --system-notifications-message-color: var(
      --system-notifications-error-message-color
    );
    --system-notifications-close-icon-color: var(
      --system-notifications-error-close-icon-color
    );
    --system-notifications-close-icon-hover-color: var(
      --system-notifications-error-close-icon-hover-color
    );
  }
}

.close-icon {
  bottom: 0;
  top: 0;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: absolute;
  right: var(--system-notifications-close-icon-spacing-right);

  & > svg {
    fill: var(--system-notifications-close-icon-color);
    height: var(--system-notifications-close-icon-size);
  }
  &:hover {
    & > svg {
      fill: var(--system-notifications-close-icon-hover-color);
    }
  }
}

.title {
  color: var(--system-notifications-title-color);
  font-size: var(--system-notifications-title-font-size);
  font-weight: var(--system-notifications-title-font-weight);
}

.message {
  color: var(--system-notifications-message-color);
  font-size: var(--system-notifications-message-font-size);
  font-weight: var(--system-notifications-message-font-weight);
}
